}


.Board {
    width: 96%;
    background: red;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: stretch;
            align-items: stretch;
    -webkit-flex-flow: column;
            flex-flow: column;
    height: 100vh;
    padding: 0;
    margin: 0;
}

.Board-Editor {
    background: green;
    -webkit-flex: 50% 1;
            flex: 50% 1;
    padding: 0;
    margin: 0;
}

.Board-Preview {
    background: blue;
    -webkit-flex: 50% 1;
            flex: 50% 1;
    padding: 0;
    margin: 0;
}

.Board-Editor-TextArea {
    border: none;
    outline: none;
    padding: 0;
    margin: 0;
    width: 100%;
    height: 100%;
    background: #181818;
    color: #efefef;
    padding: 24px;
    font-family: 'Ubuntu', Helvetica;
    font-size: 16px;
    font-weight: 300;
    line-height: 24px;
    resize: none;
}

.Board-Preview-TextArea {
    border: none;
